body{
  background-color: rgb(0,128,84);
}

.App{
  min-height: 100%;
  overflow: hidden;
}

.backImage{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.5;
}

.frontContent{
  z-index: 2;
  position: relative;
  height: 100%
}

#mapContainer {
    margin: 20px auto;
}
.navigation{
    height: 60px;
    display: flex;
    justify-content: space-between;
    background-color: black;
    width:100%;
    margin: 0px;
    padding:5px;
    box-shadow: 0px 10px 10px whitesmoke, 0px 10px 10px grey;
}

.Brand{
    font-size: 35px;
    padding-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo{
    color: rgb(0,128,84);
    display: inline;
    margin-left: 20px;

}

a, a:visited{
    color: rgb(0,128,84);
    text-decoration: none;
}

#alertLink{
    padding-right: 35px;
    font-size: 25px;
}

.rightLinks{
    display:flex;
    justify-content: center;
    align-items: center;
}
.controls {
    display: flex;
    min-width: 100%;
    margin: 0 auto;
    justify-content: space-evenly;
}
.controlBar{
    min-width: 100%;
    margin-bottom: 5px;
}

button {
    background-color: black;
    border-color: black;
    height: 100%;
    width: 100px;
    font-size: 20px;
    color: rgb(250,214,23);
    box-shadow: 2px 2px 10px rgb(250,214,23);
    border-radius: 6px;
}

button:hover {
    cursor: pointer;
}
.alertsTab {
    border: 3px solid black;
    margin: 0 auto;
    background-color: black;
}

#tableDiv{
    height: 100vh;
    margin-top: 20px;
}

th {
    border: 3px solid black;
    padding: 25px;
    text-align: center;
    font-weight: bolder;
    background-color: rgb(0,128,84);
    color:rgb(250,214,23)

}

.alertHead:hover {
    cursor: pointer;
}

td {
    border: 3px solid black;
    text-align: center;
    font-weight: bold;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    background-color: rgb(0,128,84,.7);
    color: rgb(250,214,23);
}

.tableTitle {
    text-align: center;
    color: rgb(250,214,23);
    font-size: 30px;
    align-items: center;
    padding: 0%;
}

.tableTitle:hover{
    cursor: pointer;
}

tr:nth-child(n + 11){
    display:none
}
